    created(): void {
        this.getNation()
        this.getNation()
        this.getSex()
        this.getRegionTree()
        this.getRelation()
        this.getColor()
    }
    backClose(){
        this.$emit(`close`)
    }
    openMemberDialog(){
        this.addMemberVisible=true
        this.getRegionTree()
    }
    handleStartTime(date){
        this.proprietorDetialEdit.proprietorAndRoom.proprietorAndRoomOccupation.chargingDate=date
    }
    closeMemberDialog(){
        this.addMemberVisible=false
        this.addMemberInfo={
